import NavBar from './components/nav-bar/nav-bar';
import {Switch, Route} from 'react-router-dom';
import About from "./pages/about";
import Main from "./pages/main";
import './App.scss';
import Portfolio from "./pages/portfolio";
import Achievements from "./pages/achievements";
import Contacts from "./pages/contacts";


function App() {
    return (
        <div className="App">
            <div className="app-wrapper">
                <NavBar/>
                <section className="main-content">
                    <div className="main-content__wrapper">
                        <div className="main-content__wrapper-inner">
                            <Switch>
                                <Route path='/dh-portfolio/' exact>
                                    <Main/>
                                </Route>
                                <Route path='/dh-portfolio/about' exact>
                                    <About/>
                                </Route>
                                <Route path='/dh-portfolio/portfolio' exact>
                                    <Portfolio/>
                                </Route>
                                <Route path='/dh-portfolio/achievements' exact>
                                    <Achievements/>
                                </Route>
                                <Route path='/dh-portfolio/contacts' exact>
                                    <Contacts/>
                                </Route>
                            </Switch>
                        </div>
                    </div>
                </section>
            </div>
        </div>
    );
}

export default App;
